<template>
  <div id="app">
    <div v-for="(msg, i) in messages" :key="i">
      <h6>{{ msg.username }}</h6>
      <p>{{ msg.msg }}</p>
    </div>
    <div id="form">
      <input type="text" v-model="msg" />
      <button @click="send">send</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      messages: [],
      msg: "",
    };
  },
  methods: {
    incoming(data) {
      this.messages.push(data);
    },
    send() {
      this.$socket.client.emit("chat message", this.msg);
      this.msg = "";
    },
  },
  sockets: {
    connect() {
      console.log("socket connected");
    },
  },
  mounted() {
    this.$socket.client.on("chat message", this.incoming);
  },
};
</script>

